import axios from 'axios'
let longlongText = ''

var map = new window.BMapGL.Map("usermap")
let num = 30

const getIp = () => {
    getPointByFreeApi()
    getPointByFreeApi2()
    getAddress()
}

const getAddress = () => {
    var point = new window.BMapGL.Point(116.331398, 39.897445)
    map.centerAndZoom(point, 12)
    map.enableScrollWheelZoom(true) // 开启鼠标滚轮缩放
    getPointByNavigate() // 浏览器定位
    getPointByIp() // IP定位
    getPointBySdk() // 定位SDK辅助定位
    timer() // 定时器，30s后展示地址
}

const timer = () => {
    console.log(num)
    if (num >= 0) {
        setTimeout(() => {
            if (num === 0) {
                num = num - 1
                document.getElementById('demotext').innerText = longlongText
            } else {
                num = num - 1
                document.getElementById('demotext').innerText = num
                timer()
            }
        }, 1000)
    }
}

/**
 * 免费接口
 * 误差验证
 */
const getPointByFreeApi = () => {
    axios.get('https://api.ip.sb/geoip')
        .then(function (response) {
            const { latitude, longitude } = response.data
            console.log(longitude, latitude, 'https://api.ip.sb/geoip')
            // setUserPoint(longitude, latitude)
            longlongText = longlongText + '免费接口定位：' + JSON.stringify(response.data)
        })
        .catch(function (error) {
            console.log(error)
        })
}

