/**
 * 浏览器定位
 */
const getPointByNavigate = () => {
    var geolocation = new window.BMapGL.Geolocation()
    geolocation.getCurrentPosition(function (r) {
        if (this.getStatus() === 0) {
            var mk = new window.BMapGL.Marker(r.point)
            map.addOverlay(mk)
            map.panTo(r.point)
            console.log('浏览器定位：您的位置：' + r.point.lng + ',' + r.point.lat)
            console.log('浏览器定位：您的位置：' + r.address.province + r.address.city + r.address.district + r.address.street + r.address.street_number)
            longlongText = longlongText + '浏览器定位：您的位置：' + r.address.province + r.address.city + r.address.district + r.address.street + r.address.street_number
        }
        else {
            alert('failed' + this.getStatus())
        }
    })
}

/**
 * IP定位
 */
const getPointByIp = () => {
    function myFun(result) {
        // var cityName = result.name
        // map.setCenter(cityName)
        console.log("IP定位：您的位置：", result)
        console.log("IP定位：您的位置：", result.name)
        longlongText = longlongText + "IP定位：您的位置：" + result.name
    }
    var myCity = new window.BMapGL.LocalCity()
    myCity.get(myFun)
}

/**
 * 定位SDK辅助定位
 */
const getPointBySdk = () => {
    var geolocation2 = new window.BMapGL.Geolocation()
    // 开启SDK辅助定位
    geolocation2.enableSDKLocation()
    geolocation2.getCurrentPosition(function (r) {
        if (this.getStatus() === 0) {
            var mk = new window.BMapGL.Marker(r.point)
            map.addOverlay(mk)
            map.panTo(r.point)
            console.log('定位SDK辅助定位：您的位置：' + r.point.lng + ',' + r.point.lat)
            console.log('定位SDK辅助定位：您的位置：' + r.address.province + r.address.city + r.address.district + r.address.street + r.address.street_number)
            longlongText = longlongText + '定位SDK辅助定位：您的位置：' + r.address.province + r.address.city + r.address.district + r.address.street + r.address.street_number
        }
        else {
            alert('failed' + this.getStatus())
        }
    })
}

